.wcf-mega-menu-trigger {
  cursor: pointer;
  display: inline-block;
  padding: 2px 5px;
  background: #f12529;
  color: #fff;
  font-weight: 400;
  font-size: 11px;
  line-height: 15px;
  overflow: hidden;
  visibility: hidden;
  vertical-align: middle;
  margin: -4px -25px 0 5px;
  transition: 0.4s;
  opacity: 0;
}
.wcf-mega-menu-trigger .dashicons {
  float: left;
  font-size: 12px;
  line-height: 16px;
  width: 16px;
  height: 16px;
}

.menu-item.menu-item-depth-0:hover .wcf-mega-menu-trigger {
  opacity: 1;
  visibility: visible;
}

.wcf-mega-menu-popup {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  display: flex;
  visibility: hidden;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  transition: all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  transform: scale(1.1);
  opacity: 0;
  background-color: rgba(0, 0, 0, 0.8);
}

.wcf-mega-menu-popup.open-popup {
  visibility: visible;
  opacity: 1;
  transform: scale(1);
}

.wcf-mega-menu-popup-close {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  background-color: transparent;
}

.wcf-mega-menu-popup-content {
  position: relative;
  z-index: 9;
  width: 100%;
  max-width: 510px;
  border-radius: 4px;
  background-color: #FFFFFF;
}
.wcf-mega-menu-popup-content .wcf-mega-menu-popup-close-btn {
  position: absolute;
  right: -8px;
  top: -8px;
  background: #fff;
  width: 20px;
  height: 20px;
  text-align: center;
  border-radius: 100%;
  font-size: 10px;
  cursor: pointer;
  transition: 0.4s;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.25);
}
.wcf-mega-menu-popup-content ul.mega-menu-popup-tab-menu {
  display: flex;
  margin: 0;
  padding: 0;
  border-bottom: 1px solid #dfe7f1;
  list-style: none;
  justify-content: center;
}
.wcf-mega-menu-popup-content ul.mega-menu-popup-tab-menu li {
  margin: 0;
}
.wcf-mega-menu-popup-content ul.mega-menu-popup-tab-menu li a {
  display: inline-block;
  padding: 10px 15px;
  text-decoration: none;
  color: #222222;
  font-size: 16px;
  font-weight: 500;
  border-bottom: 2px solid transparent;
  box-shadow: none;
}
.wcf-mega-menu-popup-content ul.mega-menu-popup-tab-menu li a.active {
  border-bottom: 2px solid #EA2958;
}
.wcf-mega-menu-popup-content .mega-menu-popup-tab-content {
  padding: 15px;
  margin-bottom: 15px;
}
.wcf-mega-menu-popup-content .mega-menu-popup-tab-pane {
  display: none;
}
.wcf-mega-menu-popup-content .mega-menu-popup-tab-pane.active {
  display: block;
}
.wcf-mega-menu-popup-content .mega-menu-popup-tab-pane ul li {
  margin: 15px 0 0 0;
  border-bottom: 1px solid #dfe7f1;
  padding-bottom: 15px;
  display: grid;
  grid-template-columns: 1fr 2fr;
  align-items: center;
  gap: 20px;
}
.wcf-mega-menu-popup-content .mega-menu-popup-tab-pane ul li label {
  font-weight: 600;
}
.wcf-mega-menu-popup-content .mega-menu-popup-input {
  border: 1px solid #d3dbe7;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: #f8fbff;
  color: #656565;
}
.wcf-mega-menu-popup-content .mega-menu-popup-input[type=color] {
  padding: 0px 2px;
}